@media only screen and (max-width: 800px) {
  #cart-content {
    width: 92%;
    height: 82vh;
    background-color: rgb(243, 243, 243);
    border-radius: 12px;
    position: relative;
    transition: 0.4s;
    padding: 10px 3%;
  }

  #close-cart {
    font-size: 5vh;
  }

  #cart-items {
    overflow-y: scroll;
    height: 55vh;
    padding-right: 15px;
    padding-right: 5px;
  }
  .cart-item {
    margin-bottom: 10px;
  }
  .cart-item-info h5 {
    font-size: 12px;
  }
  .cart-item-info h6 {
    font-size: 10px;
  }

  .cart-color {
    position: absolute;
    bottom: 5px;
    left: 5px;
    height: 22px;
    width: 22px;
    border-radius: 50%;
  }

  .cart-item-photo {
    width: 90px;
    height: 13vh;
    background-position: center;
    background-size: cover;
    border-radius: 10px 0 0 10px;
    position: relative;
  }

  .cart-item-right h4 {
    font-size: 16px;
  }
  .cart-item-right button {
    font-size: 14px;
    padding: 6px 14px;
  }

  #cart-buttons {
    margin: 10px auto 0px auto;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
  }
  #empty-cart {
    padding: 5px 12px;
    border: none;
    outline: none;
    border-radius: 5px;
    font-size: 14px;
    cursor: pointer;
  }
  .cart-buttons-total h5 {
    font-size: 20px;
    margin-right: 0px;
    text-align: center;
  }
  .cart-buttons-total button {
    font-size: 18px;
  }
}
