@media only screen and (max-width: 800px) {
  .single-product {
    width: 80%;
    flex-direction: column;
    align-items: center;
    height: fit-content;
  }
  .back-to-products {
    position: relative;
  }

  .single-product-left {
    width: 100%;
    height: 300px;
    margin-bottom: 8px;
  }
  .single-product-right {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  .single-product-right h3 {
    text-align: center;
    font-size: 30px;
    margin-bottom: 10px;
  }

  .single-product-right p {
    font-size: 0.9rem;
    text-align: center;
  }

  .single-product form {
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  .form-counter {
    margin: 5px auto;
  }
  .add-to-cart {
    margin-top: 10px;
    margin-left: auto;
    margin-right: auto;
    padding: 8px 18px;
  }

  .modal {
    z-index: 1;
    text-align: center;
    top: 5vh;
  }
  .modal h4 {
    font-size: 21px;
  }
}
